@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Satisfy&display=swap");
@keyframes fadeUp {
  0% {
    transform: translateY(5rem);
    opacity: 0;
  }
}
* {
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
  transition: all 0.2s linear;
  text-decoration: none;
  border: none;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 8.5rem;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: #fff;
}
html::-webkit-scrollbar-thumb {
  background: #27ae60;
  border-radius: 5rem;
}

body {
  background: #eee;
}

input:focus, textarea:focus, select:focus {
  outline: none !important;
  border: none;
}

section {
  padding: 2rem 9%;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 9%;
  background: #fff;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
}
.header__logo {
  font-size: 2.5rem;
  font-weight: bolder;
  color: #130f40;
}
.header__logo i {
  padding-right: 0.5rem;
  color: #27ae60;
}
.header .navbar a {
  font-size: 1.7rem;
  margin: 0 1rem;
  color: #666;
}
.header .navbar a:hover {
  color: #27ae60;
}
.header__icons div {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4.5rem;
  border-radius: 0.5rem;
  background: #f7f7f7;
  color: #130f40;
  font-size: 2rem;
  margin-left: 0.3rem;
  text-align: center;
  cursor: pointer;
}
.header__icons div:hover {
  background-color: #27ae60;
  color: #fff;
}

.search-form {
  position: fixed;
  top: 8.5rem;
  left: 0;
  right: 0;
  height: calc(100vh - 8.5rem);
  background: #fff;
  display: none;
}
.search-form form {
  height: 7rem;
  width: 100%;
  border-bottom: 0.2rem solid #130f40;
  display: flex;
  align-items: center;
  animation: fadeUp 0.4s linear;
}
.search-form form input {
  height: 100%;
  width: 100%;
  text-transform: none;
  font-size: 1.7rem;
  color: #130f40;
  padding-right: 1rem;
}
.search-form form label {
  font-size: 3rem;
  padding-right: 1.5rem;
  color: #130f40;
  cursor: pointer;
}
.search-form form label:hover {
  color: #27ae60;
}
.search-form.active {
  display: block;
}